@include scrollbars(.5em, $dark, $gray-300);

html {
  height: 100%;
}

.flex-1 {
  flex: 1;
}

body {
  height: 100%;

  &.search {
    background: $white;
    padding: 0;
  }
}

#content {
  min-height: 100%;
}

// SVG Sprites
#svg-sprite {
  position: absolute;
  visibility: hidden;
  top: -9999999px;
  z-index: -1;
}

.svg-icon {
  height: 1rem;
  width: 1rem;
  fill: currentColor;
  display: inline-block;
  vertical-align: middle;

  &.svg-md {
    height: 1.5rem;
    width: 1.5rem;
  }

  &.svg-lg {
    height: 2rem;
    width: 2rem;
  }

  &.svg-xl {
    height: 2.5rem;
    width: 2.5rem;
  }
}

// Responsive Google Recaptcha

// allows css aligning the captcha
.g-recaptcha {
  display: inline-block;
}

// responsive sizing on smaller devices
@media screen and (max-height: 575px) {
  #rc-imageselect, .g-recaptcha {
    transform: scale(0.75);
    transform-origin: 0 100;
  }
}



.navbar-categories {
  .nav-link {
    padding: .4rem .5rem;
    @include border-radius($border-radius);
    color: $gray-900;

    &.active {
      color: $primary;
      img, .svg-icon {
        mix-blend-mode: difference;
      }
    }
  }
}

.site-logo {
  width: $logo-width;
  height: auto;
}

.active-engine {
  color: $body-color;
}

.page-content {
  padding: 1rem 0;
  word-spacing: .1rem;
  letter-spacing: .01rem;
}


.page-flex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100%;

  .page-flex-content {
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem 0;
  }
}

.engine-tabs-wrap {
  border-bottom: $nav-tabs-border-width solid $nav-tabs-border-color;
  margin-bottom: .4rem;
  background: #fff;
  &:after {
    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 75%, rgba(255, 255, 255, 1) 100%);
    right: 0;
    height: 43px;
    width: 50px;
    content: '';
    position: absolute;
    top: 0;
  }

  position: relative;
}

.engine-overflow-hidden {
  overflow: hidden;
  height: 43px;
}

.engine-tabs {
  border: 0;
  height: 100px;
  // Make it scrollable via touch
  white-space: nowrap;
  overflow-y: hidden;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: none;
  flex-wrap: nowrap;


  .nav-link {
    border: 0;
    border-bottom: 3px solid transparent;
    color: $gray-600;
    padding: .7rem .7rem;

    @include hover-focus {
      color: $gray-500;
      border-color: transparent;
    }

    &.active {
      border-color: $secondary;
      background: transparent;
    }
  }
}

.searchbox-container {
  max-width: 1500px;
}

.search-box {
  background: $body-bg;
  padding: 1rem 0;

  .searchbox-prepend {
    border: 0;
    background: none;
    margin-right: 1rem;
  }

  .form-control, .btn {
    height: 43px;
  }

  .form-control {
    @include border-radius(0);
    @include box-shadow($searchbox-shadow);
    border-color: $primary;
    border-width: 2px;
    border-right: 0;
    @include hover-focus {
      z-index: auto;
      box-shadow: $searchbox-shadow;
    }
  }

  .btn {
    padding: 0rem 2rem;box-shadow: $searchbox-shadow;
    @include border-radius(0);
  }

  .searchbox-logo {
    width: 100px;
    height: auto;
  }
}

.gsc-webResult .gsc-result {
  border: 0!important;
  margin: 0!important;
}

.gs-result .gs-title, .gs-result .gs-title * {
  color: #003dbf!important;
}

.gsc-url-top {
  text-overflow: ellipsis!important;
  white-space: nowrap!important;
}

.gsc-selected-option-container {
  background: #fff!important;
  border: 1px solid $input-border-color !important;
  box-shadow: none!important;
  color: $text-muted!important;
  font-weight: normal!important;
  font-size: $font-size-base!important;
}


.infobox {
  overflow: hidden;
  max-height: 80px;

  &.infobox-expanded {
    max-height: 100%;
  }
}

.upside-down {
  transform: rotate(180deg);
}

/**
 * Setup keyframes for pulsing animation
 */
 @-webkit-keyframes loadingPlaceholders {
  0% {
    background-color: lightgray;
  }
  50% {
    background-color: #e5e5e5;
  }
  100% {
    background-color: lightgray;
  }
}
@keyframes loadingPlaceholders {
  0% {
    background-color: lightgray;
  }
  50% {
    background-color: #e5e5e5;
  }
  100% {
    background-color: lightgray;
  }
}

/**
 * Style the placeholder
 */
 .placeholder {
  -webkit-animation: loadingPlaceholders 1.5s ease-in infinite;
  animation: loadingPlaceholders 1.5s ease-in infinite;
  background-color: #e5e5e5;
}

.placeholder-heading {
  height: 1.5em;
  width: 55%;
  margin-bottom: 1em;
}

.placeholder-sentence {
  height: 1em;
  margin-bottom: 0.5em;
}

.placeholder-paragraph {
  height: 3em;
  margin-bottom: 2rem;
}

.placeholder-sentence-last {
  width: 85%;
  margin-bottom: 2rem;
}

.btn-dload {
  padding: 1px;
}


.abstract-img {
  float: right;
  width: 100px;
  margin-left: 1rem;
  border-radius: $border-radius;
  height: auto;
}


.category-icon {
  width: 16px;
  height: auto;
}

.carousel-item,
.carousel-inner,
.carousel-inner img {
  height: 100%;
  @include border-radius();
}

.carousel-item {
  text-align: center;
}

.home-search-box-wrapper {
  .search-box-container {
    max-width: 600px;
  }

    .home-alt-search-box {

    .form-control, .btn {
      height: 55px;
    }

    .form-control {
      @include border-radius($border-radius !important);
      border-color: $primary;
      border-right: 0;
      box-shadow: 0 3px 6px 0 rgba(0,0,0,.1), 0 1px 3px 0 rgba(0,0,0,.08);
      @include hover-focus {
        z-index: auto;
        border-color: #99999F;
      }
    }

    .btn {
      padding: 0rem 2rem;

      @include hover-focus {
        box-shadow: none;
      }
    }
  }

}

.hero-header {
  min-height: 4rem;
  padding: 1rem .5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #fff;
}


.site-navbar {
  background: $white;
  width: 100%;
    .navbar-logo {
    width: $logo-width;
    height: auto;
  }

  transition: .1s;

  padding: .8rem 0;

  &.navbar-scrolled {
    @include box-shadow($box-shadow);
    //background: $white;//rgba($white, .8);
    backdrop-filter: blur(5px);
    position: fixed;
    width: 100%;
    z-index: $zindex-fixed;
    top: 0;
  }
}


.site-heading {
  font-size: 1rem;
  color: $dark;
  padding: .6rem 0;
  letter-spacing: .2rem;
  border-bottom: 1px solid $border-color;
  text-transform: uppercase;
  span {
    border-left: 6px solid $primary;
    padding: 0 .6rem;
    display: block;
  }
}

.img-zoom {
  transition: transform .2s;

  @include hover-focus {
    transform: scale(1.5);
  }
}

  .feed-logo-img {
    max-height: 20px;
    width: auto;
  }

.post-item {
  overflow: hidden;
  @include border-radius();
  position: relative;
  background: $white;

  .post-feat-image {
    width: 100%;
    position: relative;
    flex: 130;

    .post-img-link {
      display: flex;
      height: 100%;
      overflow: hidden;
      min-width: 130px;
      max-width: 130px;
    }

    .post-feat-img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      @include border-left-radius($border-radius);
    }
  }

  .post-footer {
    padding: .5rem;
  }

  .post-title {
    font-size: 1.2rem;
    color: $black;
    font-weight: 500;
    margin: 0;

    @include hover-focus {
      color: $primary;
    }
  }
}


#latestSlider {
  height: 300px;
  @include box-shadow($box-shadow);



  .caption-label {
    background: rgba($black, .8);
    display: inline;
    line-height: 2.1rem;
    font-weight: normal;
  }

}

.sidebar-block {
  margin: 0 .4rem 1rem;
  padding: .5rem 0;

    @include box-shadow($box-shadow);
    @include border-radius();

    background: $white;

  &:last-child {
    margin-bottom: 0;
  }

  .sidebar-body {
    padding: 0 .5rem;
  }

  .sidebar-heading {
    font-size: 1.2rem;
    color: $dark;
    padding: 0rem 1rem;
    letter-spacing: .1rem;
    span {
      position: relative;
      display: inline-block;
      z-index: 2;
      padding-left: 1.3rem;
      &:before {
        width: 1rem;
        height: .6rem;
        content: "";
        display: inline-block;
        background: $primary;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
      }
    }

    .svg-icon {
      float: right;
    }
  }

    .trend-item {
      padding: .5rem 0;

      a {
        font-weight: 500;
      }
    }
  }


  .home-search-box {

    .form-control, .btn {
      height:40px;
    }

    .form-control {
      @include border-radius(0);
      border-color: $primary;
      border-width: 2px;
      border-right: 0;
      @include hover-focus {
        z-index: auto;
        box-shadow: none;
      }
    }

    .btn {
      padding: 0rem 2rem;
      
      @include border-radius(0);

      @include hover-focus {
        box-shadow: none;
      }
    }
  }

  .sidebar-post-item {
    .post-feat-image {
      position: relative;
      .post-img-link {
        display: block;
        overflow: hidden;
        height: 60px;
        width: 60px;
        .post-feat-img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          @include border-radius();
        }
      }
    }

    .post-title {
      font-size: 1rem;
      color: $dark;
    }
  }

  .video-description {
    li {
      padding: .3rem 0;
    }
  }


  .autocomplete-suggestions {
    text-align: left; cursor: default;
    background: #fff;
    box-shadow: -1px 1px 3px rgba(0,0,0,.1);
    position: absolute;
    display: none;
    z-index: 9999;
    max-height: 350px;
    overflow: hidden;
    overflow-y: auto;
    box-sizing: border-box;
  }
  .autocomplete-suggestion {
    position: relative;
    padding: .6rem 1rem;
    line-height: 1rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: $gray-800;
  }
  .autocomplete-suggestion b {
    font-weight: normal;
    color: $dark;
  }
  .autocomplete-suggestion.selected {
    background: $primary;
    color: color-yiq($primary);
    b {
      color: $white;
    }
  }

.post-container {
  max-width: 980px;
  overflow: hidden;
}

.btn-share {
  padding: .5rem 1.4rem;
}

.post-single {
  @include border-radius();
  .post-single-image {
    min-height: 250px;
    max-height: 250px;
    overflow: hidden;
    background: $dark;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }

  .post-single-content {
    font-size: 1rem;
    h1 {
      font-size: 1.5rem;
    }

    img {
      max-width: 100%;
      height: auto;
      display: block;
      margin: .1rem auto;
    }
  }
}

.btn-ia-social {
  border: 0;
}

.dummy-ad {
  @extend .d-flex, .flex-column, .justify-content-center, .align-items-center, .py-4, .px-2, .text-center;
  margin: 0 auto 1rem;
  background-color: #333;
  background-image: linear-gradient(45deg, #0e0c0c, #3c3c3c);
  color: $white;
  user-select: none;
  position: relative;

  &:after {
    content: 'Ads';
    position: absolute;
    bottom: 7px;
    right: 10px;
    border-radius: 3px;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #333;
    background: lighten($yellow, 10%);
    height: 13px;
    font-size: 12px;
    margin: 0;
    width: 30px;
    display: inline-flex;
    overflow: hidden;
  }
}


  @include media-breakpoint-up(md) {
    .sidebar{
      will-change: min-height;
      transition: all ease .2s;
    }

    .inner-wrapper-sticky {
      transform: translate(0, 0);
      transform: translate3d(0, 0, 0);
      will-change: position, transform;
    }
  }

// Styles for mobile
@include media-breakpoint-down(sm) {
  .no-shadow-xs {
    box-shadow: none!important;
  }
  body {

    .sidebar-block {
      margin: .5rem 0;
    }
    &.search {
      background: $gray-100;
    }

    &.offcanvas-open {
      overflow: hidden;
    }
  }

  .sidebar {
    height: auto!important;    
    .inner-wrapper-sticky {
      transform: none!important;
      position: static!important;
    }

  }


  .post-item {
    .post-title {
      font-size: 1rem;
    }
  }

  #latestSlider {
    height: 200px;

    .caption-label {
      font-size: 1rem;
      line-height: 1.8rem;
      padding: 3px;
    }
  }

  .gsc-webResult .gsc-result {
    background: #fff!important;
    @include border-radius($border-radius!important);
    padding: .6rem .5rem!important;
    margin-bottom: 1rem!important;
    box-shadow: rgba(0, 16, 46, 0.12) 0px 2px 6px, rgba(0, 0, 0, 0.08) 0px 1px 3px!important;
  }

  .gsc-results .gsc-cursor-box {
    border: 0!important;
  }

  .engine-tabs-wrap {
    @include box-shadow($box-shadow-sm);
    border:0;
  }

  .search-box {
    background: #fff;
    border: 0;

    padding: .5rem 0;
    .searchbox-prepend {
      width: 100%;
      margin-bottom: .5rem;

      .searchbox-logo {
        margin: 0 auto;
      }
    }
    .btn {
      padding: 0 1rem;
    }

  }

  .home-search-box {
    .form-control, .btn {
      height: 40px;
    }

    .btn {
      padding: 0 .5rem;
    }

  }

  .autocomplete-suggestions {
    width: 100%!important;
  }

  .post-item {
    .post-feat-image {
      flex: 0 0 130px;
    }
  }
}

.locale-icon {
  max-width: 16px;
  height: auto;
}
